<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary" @click="showaddCateDialogVisible"
            >添加分类</el-button
          >
        </el-col>
      </el-row>
      <el-row>
        <!-- 表格 -->
        <tree-table
          class="treeTable"
          :data="catelist"
          :columns="columns"
          :selection-type="false"
          :expand-type="false"
          index-text="#"
          show-index
          border
        >
          <!-- 是否有效 -->
          <template slot="isok" slot-scope="scope">
            <i
              v-if="scope.row.cat_deleted === false"
              class="el-icon-success"
              style="color:lightgreen"
            ></i>
            <i
              v-else
              class="el-icon-error"
              :class="isok_false"
              style="color:red"
            ></i>
          </template>
          <!-- 排序 -->
          <template slot="order" slot-scope="scope">
            <el-tag v-if="scope.row.cat_level == 0" size="mini">一级</el-tag>
            <el-tag type="warning" v-if="scope.row.cat_level == 1" size="mini"
              >二级</el-tag
            >
            <el-tag type="danger" v-if="scope.row.cat_level == 2" size="mini"
              >三级</el-tag
            >
          </template>
          <!--操作-->
          <template slot="opt" slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editCate(scope.row.cat_id)"
            >
              编辑</el-button
            >
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="removCate(scope.row)"
            >
              删除</el-button
            >
          </template>
        </tree-table>
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[5, 10, 20, 50]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </el-row>
    </el-card>
    <!-- 添加分类的对话框 -->
    <el-dialog
      title="添加分类"
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="addCateCialogColse"
    >
      <!-- 添加分类的表单 -->
      <el-form
        ref="addCateFormRef"
        :model="addCateForm"
        :rules="addCateFormRules"
      >
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类">
          <el-cascader
            :options="parentCateList"
            :props="cascaderProps"
            v-model="selecteKeys"
            @change="parentCateChanged"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑分类的对话框 -->
    <el-dialog
      title="编辑分类"
      :visible.sync="editCateDialogVisible"
      width="50%"
      @close="editCateCialogColse"
    >
      <!--  -->
      <el-form :model="editForem" :rules="editCateFormRules" ref="editdelforemrRef">
        <el-form-item label="分类名称" prop="cat_name">
          <el-input
            type="text"
            v-model="editForem.cat_name"
            clearable
          ></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editCateDialoMou">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 商品分类的数据列表，默认为空
      catelist: [],
      // 查询条件
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 总数据条数
      total: 0,
      // 为table指定列的定义
      columns: [
        {
          label: '分类名称',
          prop: 'cat_name'
        },
        {
          label: '是否有效',
          // 表示，当前为模板列
          type: 'template',
          // 表示，当前这列模板的名称
          template: 'isok'
        },
        {
          label: '排序',
          type: 'template',
          template: 'order'
        },
        {
          label: '操作',
          type: 'template',
          template: 'opt'
        }
      ],
      // 添加分类对话框的显示与隐藏
      addCateDialogVisible: false,
      // 编辑分类对话框的显示与隐藏
      editCateDialogVisible: false,
      editForem: {},
      // 添加分类的表单
      addCateForm: {
        // 分类的名称
        cat_name: '',
        // 父级分类的id
        cat_pid: 0,
        // 分类的等级，默认为一级
        cat_level: 0
      },
      // 添加表单的验证
      addCateFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' },
          { min: 1, message: '不能为空', trigger: 'blur' }
        ]
      },
      editCateFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      // 父级分类的列表
      parentCateList: [],
      // 用来指定级联选择器的配置对象
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover',
        checkStrictly: true
      },
      // 选中父级分类的id组
      selecteKeys: []
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 获取分类数据
    async getCateList() {
      const { data: res } = await this.$http.get(`categories`, {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败！')
      }
      //   把数据列表赋值给catelist
      this.catelist = res.data.result
      //   将总数据的条数，赋值给total
      this.total = res.data.total
      // console.log(res)
    },
    // 监听pagesize改变事件
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    // 监听pagenum改变事件
    handleCurrentChange(newNum) {
      this.queryInfo.pagenum = newNum
      this.getCateList()
    },
    // 点击显示添加分类对话框
    showaddCateDialogVisible() {
      this.addCateDialogVisible = true
      this.getParentCateList()
    },
    // 获取分类的父级数据列表
    async getParentCateList() {
      const { data: res } = await this.$http.get(`categories`, {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取父级分类数据失败！')
      }
      this.parentCateList = res.data
      // console.log(res)
    },
    // jilian选择项发生变化时触发的事件
    parentCateChanged() {
      // 如果selecteKeys数组lenght大于0，证明有父级分类
      // 反之没有
      if (this.selecteKeys.length > 0) {
        // 为当前分类的父级id赋值
        this.addCateForm.cat_pid = this.selecteKeys[this.selecteKeys.length - 1]
        // 为当前分类的等级赋值
        this.addCateForm.cat_level = this.selecteKeys.length
        return
      } else {
        this.addCateForm.cat_pid = 0
        this.addCateForm.cat_level = 0
      }
    },
    // 点进按钮，添加新的分类
    addCate() {
      this.$refs.addCateFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          `categories`,
          this.addCateForm
        )
        if (res.meta.status !== 201) {
          return this.$message.error('添加分类失败！')
        }
        this.$message.success('添加分类成功！')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    },
    // 对话框关闭事件
    addCateCialogColse() {
      this.$refs.addCateFormRef.resetFields()
      this.selecteKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    },
    // 删除分类
    async removCate(val) {
      const confirmCate = await this.$confirm(
        '此操作将永久删除该文件, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmCate !== 'confirm') {
        return this.$message.info('已取消删除')
      }

      const { data: res } = await this.$http.delete(`categories/${val.cat_id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('删除分类失败！')
      }
      this.$message.success('删除分类成功！')
      this.getCateList()
    },
    // 编辑对话框
    async editCate(id) {
      const { data: res } = await this.$http.get(`categories/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('获取数据失败')
      }
      this.editForem = res.data
      this.editCateDialogVisible = true
    },
    editCateCialogColse() {
      this.$refs.editdelforemrRef.resetFields()
    },
    editCateDialoMou() {
      this.$refs.editdelforemrRef.validate(async valid => {
        if (!valid) return
        const {data: res} = await this.$http.put(`categories/${this.editForem.cat_id}`, {
          cat_name: this.editForem.cat_name
        })
        if (res.meta.status !== 200) {
          return this.$message.error('修改数据失败')
        }
        this.$message.success(res.meta.msg)
        this.editCateDialogVisible = false
        this.getCateList()
      })
    }
  }
}
</script>
<style lang="less" scoped>
.treeTable {
  margin-top: 15px;
  max-height: 600px;
  overflow-y: scroll;
  //  overflow-y: hidden;
}
.el-cascader {
  width: 100%;
}
</style>