<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder="请输入内容"
            v-model="queryInfo.query"
            clearable
            @clear="getCateList"
            @keyup.enter.native="getCateList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getCateList"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddpage">添加商品</el-button>
        </el-col>
      </el-row>
      <!-- table表格视图区 -->
      <el-table :data="goodslist" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column
          label="商品名称"
          prop="goods_name"
          min-width="500px"
        ></el-table-column>
        <el-table-column
          label="商品价格（元）"
          prop="goods_price"
          min-width="100px"
        ></el-table-column>
        <el-table-column
          label="商品重量"
          prop="goods_weight"
          min-width="100px"
        ></el-table-column>
        <el-table-column label="创建时间" prop="add_time" min-width="140px">
          <template slot-scope="scope">
            {{ scope.row.add_time | dataForm }}
          </template>
        </el-table-column>
        <el-table-column label="操作" width="250px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="editBy(scope.row.goods_id)">编辑</el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="removByID(scope.row.goods_id)" >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tokal"
        background
      >
      </el-pagination>
    </el-card>
    <el-dialog title="编辑商品" :visible.sync="editDialogVisible" width="30%">
    <span>这是一段信息</span>
    <span slot="footer" class="dialog-footer">
      <el-button @click="editDialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="editDialogVisible = false">确 定</el-button>
    </span>
  </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 查询数据对象
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 商品数据
      goodslist: [],
      //数据的条数
      tokal: 0,
      // 编辑商品的对话框
      editDialogVisible:false
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 获取商品了表数据
    async getCateList() {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败！')
      }
      // this.$message.success('获取商品列表成功！')
      this.goodslist = res.data.goods
      this.tokal = res.data.total
      // console.log(res.data.total)
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCateList()
    },
    // 根据id删除所选值
    async removByID(id) {
      const confirmRom = await this.$confirm(
        '此操作将永久删除该文件, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmRom != 'confirm') return this.$message.info('已取消删除！')

      const { data: res } = await this.$http.delete(`goods/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败!')
      }
      this.$message.success('删除商品成功！')

      this.getCateList()
    },
    goAddpage() {
      this.$router.push('goods/add')
    },
    // 点击打开编辑商品的对话框
    async editBy(id){
      const { data: res } = await this.$http.get(`goods/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品列表失败！')
      }
      this.$message.success('获取商品列表成功！')
      // console.log(res)
      // console.log(id)
      this.editDialogVisible = true
      
    }
  }
}
</script>
