<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>参数列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 警告框 -->
      <el-alert
        title="注意：只允许使用第三级分类参数！"
        type="warning"
        show-icon
        :closable="false"
      >
      </el-alert>
      <!-- 选择商品分类 -->
      <el-row class="cat_opt">
        <el-col>
          <span>选择商品分类：</span>
          <!--选择商品分类的级联选择框  -->
          <el-cascader
            v-model="selectCateKeys"
            :options="catelist"
            :props="cateListProps"
            @change="handleChange"
          ></el-cascader>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <!-- 选择商品分类 -->
          <el-tabs
            type="border-card"
            @tab-click="handleTsbChange"
            v-model="activeCateName"
          >
            <!-- 添加动态参数的面板 -->
            <el-tab-pane label="动态参数" name="many">
              <el-button
                type="primary"
                size="mini"
                :disabled="isbutDisabed"
                @click="addDialogVisible = true"
                >添加参数</el-button
              >
              <!-- 动态参数表格 -->
              <el-table :data="manyTableData" border stripe>
                <!-- 展开行 -->
                <el-table-column type="expand">
                  <template slot-scope="scope">
                    <el-tag
                      :key="i"
                      v-for="(tag, i) in scope.row.attr_vals"
                      closable
                      :disable-transitions="false"
                      @close="handleClose(i, scope.row)"
                    >
                      {{ tag }}
                    </el-tag>
                    <el-input
                      class="input-new-tag"
                      v-if="scope.row.inputVisible"
                      v-model="scope.row.inputValue"
                      ref="saveTagInput"
                      size="small"
                      @keyup.enter.native="handleInputConfirm(scope.row)"
                      @blur="handleInputConfirm(scope.row)"
                    >
                    </el-input>
                    <el-button
                      v-else
                      class="button-new-tag"
                      size="small"
                      @click="showInput(scope.row)"
                      >+ New Tag</el-button
                    >
                  </template>
                </el-table-column>
                <!-- 索引列 -->
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column
                  label="参数名称"
                  prop="attr_name"
                ></el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button
                      type="primary"
                      icon="el-icon-edit"
                      size="mini"
                      @click="showEditDialogBtn(scope.row.attr_id)"
                      >编辑</el-button
                    >
                    <el-button
                      type="danger"
                      icon="el-icon-delete"
                      size="mini"
                      @click="deleteParams(scope.row.attr_id)"
                    >
                      删除</el-button
                    >
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <!-- 添加静态属性的面板 -->
            <el-tab-pane label="静态属性" name="only">
              <el-button
                type="primary"
                size="mini"
                :disabled="isbutDisabed"
                @click="addDialogVisible = true"
                >添加属性</el-button
              >
              <!-- 动态参数表格 -->
              <el-table :data="onlyTableData" border stripe>
                <!-- 展开行 -->
                <el-table-column type="expand">
                  <template slot-scope="scope">
                    <el-tag
                      :key="i"
                      v-for="(tag, i) in scope.row.attr_vals"
                      closable
                      :disable-transitions="false"
                      @close="handleClose(i, scope.row)"
                    >
                      {{ tag }}
                    </el-tag>
                    <el-input
                      class="input-new-tag"
                      v-if="scope.row.inputVisible"
                      v-model="scope.row.inputValue"
                      ref="saveTagInput"
                      size="small"
                      @keyup.enter.native="handleInputConfirm(scope.row)"
                      @blur="handleInputConfirm(scope.row)"
                    >
                    </el-input>
                    <el-button
                      v-else
                      class="button-new-tag"
                      size="small"
                      @click="showInput(scope.row)"
                      >+ New Tag</el-button
                    >
                  </template>
                </el-table-column>
                <!-- 索引列 -->
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column
                  label="参数名称"
                  prop="attr_name"
                ></el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button
                      type="primary"
                      icon="el-icon-edit"
                      size="mini"
                      @click="showEditDialogBtn(scope.row.attr_id)"
                      >编辑</el-button
                    >
                    <el-button
                      type="danger"
                      icon="el-icon-delete"
                      size="mini"
                      @click="deleteParams(scope.row.attr_id)"
                    >
                      删除</el-button
                    >
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </el-col>
      </el-row>
    </el-card>
    <!-- 添加对话框 -->
    <el-dialog
      :title="'添加' + titleTxt"
      :visible.sync="addDialogVisible"
      width="30%"
      @close="addDialogClose"
    >
      <!-- 添加表单 -->
      <el-form :model="addForm" ref="addFormRef" :rules="addFormRules">
        <el-form-item :label="titleTxt" prop="attr_name">
          <el-input
            type="text"
            v-model="addForm.attr_name"
            clearable
          ></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addParams">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑对话框 -->
    <el-dialog
      :title="'修改' + titleTxt"
      :visible.sync="showEditDialogVisble"
      width="30%"
      @close="editDialogClose"
    >
      <!-- 添加表单 -->
      <el-form :model="editForm" ref="editFormRef" :rules="addFormRules">
        <el-form-item :label="titleTxt" prop="attr_name">
          <el-input
            type="text"
            v-model="editForm.attr_name"
            clearable
          ></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="showEditDialogVisble = false">取 消</el-button>
        <el-button type="primary" @click="editParams">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 商品分类列表数据
      catelist: [],
      cateListProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
        // checkStrictly:true
      },
      //级联选择框中，显示的数组
      selectCateKeys: [],
      //  被激活标签的名称
      activeCateName: 'many',
      //  定义动态参数
      manyTableData: [],
      //  定义静态参数
      onlyTableData: [],
      //  对话框的显示和隐藏
      addDialogVisible: false,
      //  添加表单的数据
      addForm: {},
      //  添加表单的验证规则对象
      addFormRules: {
        attr_name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' }
        ]
      },
      //  修改对话框显示和隐藏
      showEditDialogVisble: false,
      // 修改的表单数据
      editForm: {}
    }
  },
  created() {
    this.getParamsList()
  },
  methods: {
    //   获取商品分类列表数据
    async getParamsList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$messgae.error('获取商品分类列表失败！')
      }
      this.catelist = res.data
    },
    // 级联选择器变化时，触发这个函数
    handleChange() {
      this.getParamsData()
    },
    // tab标签的点击事件
    handleTsbChange() {
      this.getParamsData()
    },
    // 获取参数的列表数据
    async getParamsData() {
      if (this.selectCateKeys.length !== 3) {
        this.selectCateKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      //根据所选分类的Id，和当前所处的面板,获取对应的参数
      const {
        data: res
      } = await this.$http.get(`categories/${this.cateId}/attributes`, {
        params: { sel: this.activeCateName }
      })
      if (res.meta.status !== 200) {
        return this.$messgae.error('获取数据失败！')
      }

      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 控制文本框的显示和隐藏
        item.inputVisible = false
        // 文本框显示的内容
        item.inputValue = ''
      })

      if (this.activeCateName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 监听对话框关闭事件
    addDialogClose() {
      this.$refs.addFormRef.resetFields()
    },
    editDialogClose() {
      this.$refs.editFormRef.resetFields()
    },
    // 点击添加
    addParams() {
      this.$refs.addFormRef.validate(async val => {
        if (!val) {
          return
        }
        const { data: res } = await this.$http.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.addForm.attr_name,
            attr_sel: this.activeCateName
          }
        )
        if (res.meta.status !== 201) {
          return this.$message.error('添加数据失败！')
        }
        this.$message.success('添加成功')
        this.addDialogVisible = false
        this.getParamsData()
      })
    },
    //
    async showEditDialogBtn(val) {
      const { data: res } = await this.$http.get(
        `categories/${this.cateId}/attributes/${val}`
      )
      if (res.meta.status !== 200) {
        return this.$messgae.error('获取商品分类列表失败！')
      }
      this.showEditDialogVisble = true
      this.editForm = res.data
    },
    // 点击确认，提交修改
    editParams() {
      this.$refs.editFormRef.validate(async val => {
        if (!val) {
          return
        }
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
          {
            attr_name: this.editForm.attr_name,
            attr_sel: this.activeCateName
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('修改数据失败！')
        }
        this.$message.success('修改成功')
        this.getParamsData()
        this.showEditDialogVisble = false
      })
    },
    // 删除当前id的值
    async deleteParams(id) {
      const confirmParams = await this.$confirm(
        '此操作将永久删除内容, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmParams !== 'confirm') {
        return this.$message.info('删除已取消！')
      }
      const { data: res } = await this.$http.delete(
        `categories/${this.cateId}/attributes/${id}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败！')
      }
      this.$message.success('删除商品分类成功！')
      this.getParamsData()
    },
    // 文本框失去焦点或按下 enter 是触发的事件
    async handleInputConfirm(row) {
      if (row.inputValue.trim().length == 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      //如果没有return，则证明输入的内容，需要做后续处理
      row.attr_vals.push(row.inputValue.trim())
      row.inputValue = ''
      // 发出请求。保存数据
      this.saveAttrVals(row)
      row.inputVisible = false
    },
    // 点击按钮，显示文本输入框
    showInput(Visible) {
      Visible.inputVisible = true
      //让文本框自动获得焦点
      // SnextTick方法的作用，就是当页面上元素被重新渲染之后,才会指定回调函数中的代码

      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 将对attr_vals的操作,保存到数据库
    async saveAttrVals(row) {
      const { data: res } = await this.$http.put(
        `categories/${this.cateId}/attributes/${row.attr_id}`,
        {
          attr_name: row.attr_name,
          attr_sel: row.attr_sel,
          attr_vals: row.attr_vals.join(' ')
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('修改参数项失败')
      }
      this.$message.success('修改参数项成功')
    },
    //删除对应的参数可选项
    handleClose(id, row) {
      row.attr_vals.splice(id, 1)
      this.saveAttrVals(row)
    }
  },
  computed: {
    // 如果按钮需要被禁用，则换回true，否则返回fasle
    isbutDisabed() {
      if (this.selectCateKeys.length !== 3) {
        return true
      }
      return false
    },
    //  当前选中数据的id
    cateId() {
      if (this.selectCateKeys.length === 3) {
        return this.selectCateKeys[2]
      }
      return null
    },
    // 动态标题
    titleTxt() {
      if (this.activeCateName === 'many') {
        return '动态参数'
      }
      return '静态属性'
    }
  }
}
</script>
<style lang="less" scoped>
.cat_opt {
  margin: 15px 0;
}
.el-tag {
  margin: 7px;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: center;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
</style>